import * as React from "react";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import { useRouter } from "next/navigation";

export const metadata = {
  title: "Login",
}

export default function LoginPage() {
  const [username, setUsername] = React.useState("");
  const [password, setPassword] = React.useState("");
  const [error, setError] = React.useState("");
  const router = useRouter();

  const handleLogin = async (e) => {
    e.preventDefault();
    setError("");
    const res = await fetch("http://localhost:8080/api/users/login", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ username, password }),
    });
    if (res.ok) {
      // Optionally store user info in localStorage/session
      router.push("/all-files"); // Redirect to dashboard
    } else {
      setError("Invalid username or password");
    }
  };

  return (
    <form
      onSubmit={handleLogin}
      style={{
        minHeight: "90vh",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center"
      }}
    >
      <img src="/images/photoLodgeLogo.png" alt="PhotoLodge Logo" width={175} height={175} />
      <h1>Welcome to PhotoLodge</h1>
      <TextField
        label="Username"
        variant="outlined"
        value={username}
        onChange={e => setUsername(e.target.value)}
        style={{ width: 210, margin: "8px 0" }}
      />
      <TextField
        label="Password"
        variant="outlined"
        type="password"
        value={password}
        onChange={e => setPassword(e.target.value)}
        style={{ width: 210, margin: "8px 0" }}
      />
      <Button type="submit" variant="contained" style={{ width: 210, margin: "8px 0" }}>
        Login
      </Button>
      {error && <div style={{ color: "red" }}>{error}</div>}
    </form>
  );
}